


*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body{
  background-color: #fafafa;
}
header{
  width: 100%;
  background-color: #1a1919;
  box-shadow: 0px 0px 5px 1px #eeeaea;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 0 10px 0;
  color: #fff;
}
ul{
  list-style-type: none;
}
header nav h3{
  font-size: 2rem;
}
header nav input{
  width: 50%;
  padding: 15px;
  border-radius: 36px;
  border: none;
  /* box-shadow: 0px 0px 5px 1px #eeeaea; */

}
header nav,header>:nth-child(2){
  width: 100%;
  max-width: 1300px;
  margin:auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}
/* header nav{
  margin-top: 20px;
} */
header>:nth-child(2){
  margin-top: 10px;
}

.icons svg{
  width: 40px;
  height: 40px;
  cursor: pointer;
}
.icons svg:hover{
  opacity: 0.8;
}
.icons>:nth-child(2){
  position: relative;
}
.icons>:nth-child(2)>:nth-child(2){
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;

  color: #fff;
  background-color: #000;
  top: -5px;
  right: -5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.icons,.btn ul,.categories ul li{
  display: flex;
  gap: 10px;
  align-items: center;
}
.btn button,.btn ul li{
  padding: 10px 89px;
  background-color: #fff;
  /* box-shadow: 0px 0px 5px 1px #eeeaea; */
  color: #000;
  border: none;
  font-size: 1.2rem;

}
.btn ul li:hover{
  background-color: #615f5f;
  color: #fff;
  cursor: pointer;
  
}
.categories{
  width: 100%;
  max-width: 1300px;
  display: flex;
  flex-direction: column;
  margin: auto;
  position: relative;
  margin-top: 10px;
}
.categories ul{
  background-color: #fff;
  font-size: 1.2rem;
  position: absolute;
  top:0;
  left: 10px;
  padding: 20px 0;
  box-shadow: 0px 0px 5px 1px #eeeaea;
  
}
.categories ul li{
  padding: 10px 50px;
  width: 100%;
  cursor: pointer;
}
.categories ul li:hover{
  background-color: #1a1919;
  color: #fff;
}
.categories img{
  width: 30px;

}

main{
  width: 100%;
  max-width: 1300px;
  margin: auto;
  padding-left: 280px;
  padding-right: 20px;

}
.slider{
  display: flex;
  overflow: hidden;
  width: 100%;
  height: 590px;
  
  box-shadow: 0px 0px 5px 1px #eeeaea;
  position: relative;
}
.slider article{
  position: absolute;
  display: grid;
  height: 100%;
  grid-template-columns: 2fr 1fr;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  padding: 20px;
  transition: all 0.5s ease;
}
.activeSlide{
  transform: translateX(0);
  opacity: 1;
}
.nextSlide{
  transform: translateX(100%);
  opacity: 0;
}
.lastSlide{
  transform: translateX(-100%);
  opacity: 0;
}
.slider article h4{
  font-size: 3rem;
}
.slider article p{
  width: 80%;
  padding: 10px 0;
}

.slider article img{
  width: 300px;
  height: 400px;
}

.products,.topCategories,.newArrivals,.discounts{
  width: 100%;
  max-width: 1300px;
  /* overflow: hidden; */
  margin: 30px auto;
}
.flash>:first-child,.topCategories>:first-child,.newArrivals>:first-child,.discounts>:first-child{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
  font-size: 2rem;
  padding: 50px 20px;
}
.flash>:first-child>:first-child,.discounts>:first-child>:first-child{
  color: red;
}
.cards{
  padding: 0 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
}
.flash .slick-next:before,.flash .slick-prev:before {
  color: #000;
  font-size: 2rem;
  /* position: absolute; */

}
.flash .slick-active {
  width: 270px !important;
}


.flash .slick-current{
  margin-left: 75px !important;
}

.cards article{
  width: 250px ;
  height: 380px ;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  margin: 0 10px;
  box-shadow: 0px 0px 5px 1px #eeeaea;
  transition: all 0.3s ease;
}

.cards article>:first-child{
  background-color:rgb(250, 49, 49);
  width: 40%;
  border-radius: 36px;
  padding: 5px;
  color: #fff;
  /* font-weight: bold; */
  text-align: center;
  margin: 10px;
}

.cards article img{
  width: 100%;
}

.cards article>:nth-child(3){
  font-size: 1.2rem;
  padding: 0 10px;
}
.cards article>:nth-child(4){
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  font-size: 1.2rem;
  padding: 10px;
}
.cards article>:nth-child(4) p{
  background-color: #000;
  padding: 10px;
  border-radius: 5px;
  font-weight: bold;
  color: #fff;
}
.cards article>:nth-child(4) p:hover{
  cursor: pointer;
  opacity: 0.8;
}

.cards article:hover{
  background-color: #1a1919;
  color: #fff;
}
.cards article:hover p{
background-color: #fff;
color: #000;
}

.catCards{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 30px;
}

.catCards article{
  position: relative;
  padding: 15px;
  background-color: #fff;
  
  box-shadow: 0px 0px 5px 1px #eeeaea;

}
.catCards article p{
  position: absolute;
  background-color: rgba(255, 255, 255, 0.1);
  padding: 5px;
  border-radius: 36px;
  box-shadow: 0px 0px 5px #868585;
}
.catCards article img{
  width: 80%;
}

.catCards article>:nth-child(2){
  top: 10%;
  left: 10%;
}
.catCards article>:nth-child(3){
  right: 10%;
  bottom: 10%;
}

.newCards{
  width: 100%;
  max-width: 1300px;
  margin: auto;
  background-color: #fff;
  padding: 15px;
  box-shadow: 0px 0px 4px 1px #eeeaea;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
}
.newCards article img{
  width: 190px;
  box-shadow: 0px 0px 5px 1px #eeeaea;
  border-radius: 10px;

}
.newCards article p,.dCards article p{
  padding-left: 10px;
}
.newCards article>:nth-child(2),.dCards article>:nth-child(2){
  font-weight: bold;
  padding: 10px;
}



.dCards{
  width: 100%;
  height: 100%;
  max-width: 1250px;
  margin: auto;
  display: flex; 
  flex-wrap: wrap;
   justify-content: center;
  align-items: center;
  gap: 10px;
  /* overflow-X: hidden;
  overflow-y:auto; */
}
.dCards article{
  background-color: #fff;
  padding: 10px;
  /* margin: 10px; */
  /* margin-left: 10px; */
  transition: all 0.3s ease;
  /* height:fit-content; */
  box-shadow: 0px 0px 5px 1px #eeeaea;

}
.dCards article img{
  width: 200px;
}

.brands{
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  /* gap: 10px; */
}
.names>:first-child{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}
.names>:first-child h3,.electronics>:first-child{
  font-size: 1.5rem;
  /* padding: 20px; */
}
.electronics>:first-child{
  padding: 20px;
}
.names>:first-child>:nth-child(2){
  font-size: 1.5rem;
}
.names>:first-child p{
  opacity: 0.8;

}
.names{
  background-color: #fff;
  padding: 10px 0;
  box-shadow: 0px 0px 5px 1px #eeeaea;
  height: max-content;
}

.names article{
  width: 100%;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 1.2rem;
  padding: 10px 81px;
}
.names article:hover{
  background-color: #1a1919;
  color: #fff;
  cursor: pointer;
}

.electronics{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 20px;
}
.electronics article{
  width: 250px ;
  height: 380px ;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  margin: 0 10px;
  box-shadow: 0px 0px 5px 1px #eeeaea;
  transition: all 0.3s ease;
}
.electronics article>:first-child{
  background-color:rgb(250, 49, 49);
  width: 40%;
  border-radius: 36px;
  padding: 5px;
  color: #fff;
  /* font-weight: bold; */
  text-align: center;
  margin: 10px;
}
.electronics article img{
  width: 100%;
}

.electronics article>:nth-child(3){
  font-size: 1.2rem;
  padding: 0 10px;
}
.electronics article>:nth-child(4){
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  font-size: 1.2rem;
  padding: 10px;
}
.electronics article>:nth-child(4) p{
  background-color: #000;
  padding: 10px;
  border-radius: 5px;
  font-weight: bold;
  color: #fff;
}
.electronics article>:nth-child(4) p:hover{
  cursor: pointer;
  opacity: 0.8;
}

.electronics article:hover{
  background-color: #1a1919;
  color: #fff;
}
.electronics article:hover p{
background-color: #fff;
color: #000;
}

.electronics>:first-child{
  width: 100%;
}

.images{
  width: 100%;
  max-width: 1300px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  padding: 50px 20px;
  gap: 20px;
}
.images>:first-child{
  width: 30%;
}
.images>:last-child{
  width: 70%;
}


.details{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.details article{
  background-color: #fff;
  box-shadow: 0px 0px 5px 1px #eeeaea;
  text-align: center;
  padding: 20px 10px;
  transition: all 0.3s ease;
}
.details article p{
  width: 80%;
  margin: auto;
}
.details article>:nth-child(1){
  font-size: 2rem;
}
.details article>:nth-child(2){
  font-size: 1.2rem;
  padding: 10px;
  font-weight: bold;
  opacity: 0.8;
}
.details article>:nth-child(3){
  opacity: 0.8;
}
.details article:hover{
  background-color: #1a1919;
  color: #fff;
}


footer{
  width: 100%;
  background-color: #1a1919;
  margin-top: 50px;
}
footer>:first-child{
  width: 100%;
  max-width: 1300px;
  margin: auto;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 20px;
  color: #fff;
  padding: 20px;
  flex-wrap: wrap;
}
footer>:first-child>:first-child{
  width: 400px;
}
footer>:first-child>:first-child p{
  width: 70%;
  padding: 10px;
  opacity: 0.8;
}
.box>:first-child{
  padding-bottom: 10px;
}
.box ul li{
  opacity: 0.8;
  padding: 8px;
}
.box ul li:hover{
  cursor: pointer;
  opacity: 1;
}
.box p{
  opacity: 0.8;
  padding: 8px;
}

@media screen and (max-width:1000px) {
    .cards article{
      width: 200px;
      height: 300px;
    }
    .cards article img {
      width: 80%;
      margin: auto;
  }
    .electronics article{
      width: 200px;
      height: 300px;
    }
    .electronics article img {
      width: 80%;
      margin: auto;
  }
}
@media screen and (max-width:768px) {
  .btn button{
    display: none;
  }
    header>:nth-child(2){
      /* flex-wrap: wrap;
      gap: 20px; */
      justify-content: center;
    }
    .categories ul{
      font-size: 1rem;
    }
    .categories ul li{
      padding: 10px 25px;
    }
    main{
      padding-left: 220px;
    }
    .slider article h4{
      font-size: 2rem;
    }
    .slider article img{
      width: 200px;
      height: 300px;
    }
    .electronics{
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .brands .names{
      display: none;
    }
    .details{
      flex-wrap: wrap;
      width: 80%;
      margin: auto;
    }

}

@media screen and (max-width:600px) {
    header>:nth-child(2){
      display: none;
    }
    header nav{
      flex-wrap: wrap;
    }
    header nav input{
      order: 3;
      width: 80%;
      margin: auto;
      margin-top: 20px;
    }
  .categories{
      display: none;
    }
    main{
      padding: 20px;
    }
    .slider{
      height: 400px;
    }
    /* .cards article{
      width: 100px;
      height: 200px;
    } */
    .cards article img{
      width:70%;
      /* margin: 0px auto; */
    }

}

@media screen and (max-width:500px) {
  .slider article h4{
    font-size: 1.5rem;
  }
  .slider article img{
    width: 100px;
    height: 200px;
  }
  .slider{
    height: 300px;
  }
}